const uni = require('uni').default;










































import { mapState, mapGetters, mapMutations } from 'vuex'

export default {
	data(){
		return {
			listdata: []
		}
	},
	computed: {
		...mapGetters([
			'GET_CODE',
		]),
		...mapState([
			'HTTPS_URL'
		])
	},
	mounted(){
		this.getList()
	},
	methods: {
		zhendong(){
			wx.vibrateShort()
		},
		goto(id){
			uni.navigateTo({
				url: `../page/page?id=${id}`,
			})
		},
		getList(){
			let _this = this
			let storage = uni.getStorageSync('CODE')
			uni.request({
				url: `${_this.HTTPS_URL}/cardPocket/list`,
				method:'GET',
				header: {
					'Cookie': `ticket=${storage.ticket}`
				},
				success:function(res){
					if(res.data.status === 1){
						_this.listdata = res.data.data.cards
					}
				}
			})
		}
	},
}



